export default {
	name: 'uni-number-box',
	props: {
		isMax: {
			type: Boolean,
			default: false
		},
		isMin: {
			type: Boolean,
			default: false
		},
		index: {
			type: Number,
			default: 0
		},
		value: {
			type: Number,
			default: 0
		},
		min: {
			type: Number,
			default: -Infinity
		},
		max: {
			type: Number,
			default: Infinity
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: false
		},
		dataItem:{
			type: Object,
			default: null
		},
	},
	data() {
		return {
			inputValue: this.value,
			minDisabled: false,
			maxDisabled: false
		}
	},
	created(){
		this.maxDisabled = this.isMax;
		this.minDisabled = this.isMin;
	},
	computed: {

	},
	watch: {
		inputValue(number) {
			const data = {
				number: number,
				index: this.index
			}
			this.$emit('eventChange', data);
		}
	},
	methods: {
		_calcValue(type) {
			const scale = this._getDecimalScale();
			let value = this.inputValue * scale;
			let newValue = 0;
			let step = this.step * scale;
			let id = this.dataItem.id
			
			console.log(value);
			console.log('12311111111111')
			
			
			if(type === 'subtract'){
				
				
				newValue = value - step;
				console.log(newValue);
				console.log('减',newValue)
				if (newValue <= this.min){
					this.minDisabled = true;
					if(newValue){
						this.clickNum(id,newValue)
					}
					console.log('aaaa',1)
				}
				if(newValue < this.min){
					newValue = this.min
					console.log('aaaa',2)
				}
				if(newValue < this.max && this.maxDisabled === true){
					this.maxDisabled = false;
					console.log('aaaa',3)
				}
			}else if(type === 'add'){
				newValue = value + step;
				console.log('加1',newValue,this.max,value)
				if (value >= this.max){
					this.maxDisabled = true;
					newValue = this.max
					console.log('不加')
				}else{
					this.clickNum(id,newValue)
				}
				
				// if(newValue > this.max){
				// 	newValue = this.max
				// 	console.log('aaaa',2)
				// }
				if(newValue > this.min && this.minDisabled === true){
					this.minDisabled = false;
					
					console.log('aaaa',3)
				}
			}
			if(newValue === value){
				return;
			}
			this.inputValue = newValue / scale;
		},
		
		clickNum(id,newValue){
			let path = '/gzh/auth_api/change_cart_num'
			let data = {
				cartId:id,
				cartNum:newValue
			};
			getApp().http(path, data, function(res) {
				console.log(res);
			})
		},
		
		_getDecimalScale() {
			let scale = 1;
			// 浮点型
			if (~~this.step !== this.step) {
				scale = Math.pow(10, (this.step + '').split('.')[1].length);
			}
			return scale;
		},
		_onBlur(event) {
			let value = event.detail.value;
			if (!value) {
				this.inputValue = 0;
				return
			}
			value = +value;
			if (value > this.max) {
				value = this.max;
			} else if (value < this.min) {
				value = this.min
			}

			this.inputValue = value
		}
	}
}
